import { Component, Element, Fragment, Listen, Prop, h } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';
import { DEFAULT_NOTIFICATION_LINKS, MenuItemGroup, NOTIFICATION_TYPE, NotificationLinks, TRANSLATIONS } from './exports';

@Component({
  tag: 'mdx-login-flyout',
  styleUrl: 'login-flyout.scss',
  shadow: true,
})
export class LoginFlyout {
  @Element()
  host: HTMLMdxLoginFlyoutElement;

  @Prop({ reflect: true })
  notificationType: (typeof NOTIFICATION_TYPE)[keyof typeof NOTIFICATION_TYPE] = NOTIFICATION_TYPE.NONE;

  @Prop()
  userProfileName: string;

  @Prop()
  userProfileEmail: string;

  @Prop()
  accountUrl: string;

  @Prop({ reflect: true })
  open = false;

  @Prop()
  language: keyof typeof TRANSLATIONS = 'de';

  @Prop({ reflect: true })
  dark: boolean;

  @Prop()
  logout: () => void;

  @Prop()
  menuItemGroup: MenuItemGroup[] = [];

  @Prop()
  notificationLinks: NotificationLinks;

  @Listen('click', { target: 'window' })
  handleOutsideClick(event: MouseEvent) {
    if (this.open && !this.host.contains(event.target as Node)) {
      this.open = false;
    }
  }

  private formatNotification = (email_to: string | undefined) => ({
    text: TRANSLATIONS[this.language].notification?.[this.notificationType].text.replace('{email_to}', email_to || ''),
    link:
      this.notificationLinks?.notifications?.[this.notificationType] || DEFAULT_NOTIFICATION_LINKS[this.notificationType],
    linkText: TRANSLATIONS[this.language]?.notification?.[this.notificationType]?.linkText,
  });

  render() {
    return this.open ? (
      <div class="login-flyout">
        <div class="login-flyout__triangle" />
        <div class="login-flyout__title-container">
          <button
            class="login-flyout__close-button"
            aria-label={TRANSLATIONS[this.language].close}
            onClick={() => (this.open = false)}
          >
            <mdx-icon-x size={ICON_SIZES.SM} class="login-flyout__close-button__icon" />
          </button>
          <div class="login-flyout__username">{this.userProfileName}</div>
        </div>
        <mdx-login-flyout-divider dark={this.dark} />
        {this.notificationType !== 'none' && (
          <mdx-login-flyout-notification
            notificationType={this.notificationType}
            notification={this.formatNotification(this.userProfileEmail).text}
            notificationLink={this.formatNotification(this.notificationType).linkText}
            dark={this.dark}
            href={this.formatNotification(this.notificationType).link}
          >
            {this.notificationType === 'mailDeliverability' ? <mdx-verify-alert /> : <mdx-cumulus-alert />}
          </mdx-login-flyout-notification>
        )}
        {this.menuItemGroup && this.menuItemGroup.length > 0 && (
          <Fragment>
            <mdx-login-flyout-menu-group menuItemGroup={this.menuItemGroup} dark={this.dark} />
            <mdx-login-flyout-divider dark={this.dark} />
          </Fragment>
        )}
        <mdx-login-flyout-menu-item
          label={TRANSLATIONS[this.language].login.manage}
          href={this.accountUrl}
          variant="account"
          dark={this.dark}
          as="link"
        />
        <mdx-login-flyout-divider dark={this.dark} />
        <mdx-login-flyout-menu-item
          label={TRANSLATIONS[this.language].login.logout}
          as="button"
          dark={this.dark}
          menuItemClick={() => this.logout && this.logout()}
        />
      </div>
    ) : null;
  }
}
