:host .login-flyout {
  box-sizing: border-box;
  width: var(--mdx-comp-login-flyout-sizing-desktop-container-width);
  height: var(--mdx-comp-login-flyout-sizing-desktop-container-height);
  background-color: var(--mdx-comp-login-flyout-color-light-container-background-default);
  box-shadow: var(--mdx-comp-login-flyout-shadow-desktop-container-default);
  border-radius: var(--mdx-comp-login-flyout-border-radius-desktop-container-default);
  padding-bottom: var(--mdx-comp-login-flyout-spacing-desktop-container-padding-bottom);
  position: relative;
  animation: visible var(--mdx-comp-dialog-transition-fade-in-out-duration)
    var(--mdx-comp-dialog-transition-fade-in-out-timing-function) var(--mdx-comp-dialog-transition-fade-in-out-delay);
  z-index: 1001;

  &__close-button {
    display: none;
  }

  @media screen and (max-width: 768px) {
    width: var(--mdx-comp-login-flyout-sizing-mobile-container-width);
    height: var(--mdx-comp-login-flyout-sizing-mobile-container-height);
    background-color: var(--mdx-comp-login-flyout-color-light-container-background-default);
    border-radius: var(--mdx-comp-login-flyout-border-radius-mobile-container-top)
      var(--mdx-comp-login-flyout-border-radius-mobile-container-top)
      var(--mdx-comp-login-flyout-border-radius-mobile-container-bottom)
      var(--mdx-comp-login-flyout-border-radius-mobile-container-bottom);
    padding-bottom: var(--mdx-comp-login-flyout-spacing-mobile-container-padding-bottom);
    box-shadow: var(--mdx-comp-login-flyout-shadow-mobile-container-default);
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    animation: slide-in var(--mdx-comp-dialog-transition-fade-in-out-duration)
      var(--mdx-comp-dialog-transition-fade-in-out-timing-function) var(--mdx-comp-dialog-transition-fade-in-out-delay);
    z-index: 1001;
    overflow-y: auto;
    max-height: 100vh;

    &::-webkit-scrollbar {
      display: none;
    }

    &__title-container {
      position: sticky;
      top: 0;
    }

    &__close-button {
      all: unset;
      height: var(--mdx-comp-icon-button-medium-sizing-base-default);
      width: var(--mdx-comp-icon-button-medium-sizing-base-default);
      size: var(--mdx-comp-login-flyout-border-width-mobile-title-close-button-default);
      position: absolute;
      cursor: pointer;
      right: var(--mdx-comp-login-flyout-spacing-mobile-flyout-title-padding-right);
      top: var(--mdx-comp-login-flyout-spacing-mobile-flyout-title-padding-top);

      &__icon {
        color: var(--mdx-comp-login-flyout-color-light-flyout-title-close-button-color-default);

        &:hover {
          color: var(--mdx-comp-login-flyout-color-light-flyout-title-close-button-color-hover);
        }
      }
    }
  }

  &__triangle {
    width: 16px;
    height: 16px;
    background-color: var(--mdx-comp-login-flyout-color-light-container-background-default);
    rotate: -45deg;
    position: absolute;
    right: var(--mdx-comp-login-flyout-spacing-desktop-container-triangle-margin-right);
    top: -8px;

    @media screen and (max-width: 768px) {
      display: none;
    }
  }

  &__username {
    padding: var(--mdx-comp-login-flyout-spacing-desktop-flyout-title-padding-top)
      var(--mdx-comp-login-flyout-spacing-desktop-flyout-title-padding-right)
      var(--mdx-comp-login-flyout-spacing-desktop-flyout-title-padding-bottom)
      var(--mdx-comp-login-flyout-spacing-desktop-flyout-title-padding-left);
    font: var(--mdx-comp-login-flyout-typography-desktop-flyout-title-default);
    word-break: break-all;
    background-color: var(--mdx-comp-login-flyout-color-light-container-background-default);
    border-radius: var(--mdx-comp-login-flyout-border-radius-desktop-container-default);

    @media screen and (max-width: 768px) {
      padding: calc(
          var(--mdx-comp-login-flyout-spacing-mobile-flyout-title-user-name-padding-top) +
            var(--mdx-comp-login-flyout-spacing-mobile-flyout-title-padding-top)
        )
        var(--mdx-comp-login-flyout-spacing-mobile-flyout-title-padding-right)
        var(--mdx-comp-login-flyout-spacing-mobile-flyout-title-padding-bottom)
        var(--mdx-comp-login-flyout-spacing-mobile-flyout-title-padding-left);
      font: var(--mdx-comp-login-flyout-typography-mobile-flyout-title-default);
      word-break: break-all;
    }
  }
}

:host([dark]) .login-flyout {
  background-color: var(--mdx-comp-login-flyout-color-dark-container-background-default);

  &__triangle {
    background-color: var(--mdx-comp-login-flyout-color-dark-container-background-default);
  }
  &__username {
    color: var(--mdx-comp-login-flyout-color-dark-flyout-title-color-default);
    background-color: var(--mdx-comp-login-flyout-color-dark-container-background-default);
  }

  &__close-button__icon {
    color: var(--mdx-comp-login-flyout-color-dark-flyout-title-close-button-color-default);

    &:hover {
      color: var(--mdx-comp-login-flyout-color-dark-flyout-title-close-button-color-hover);
    }
  }
}

@keyframes visible {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slide-in {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}
